// AI Project Management API v1
// POST /api/v1/projects/[projectId]/ai/generate-tasks - Generate tasks using AI
// POST /api/v1/projects/[projectId]/ai/analyze - Analyze project and get AI insights
// POST /api/v1/projects/[projectId]/ai/chat - Chat with AI assistant about the project

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'

interface RouteParams {
  params: Promise<{ projectId: string }>
}

// POST - AI operations for a project
export async function POST(request: NextRequest, { params }: RouteParams) {
  try {
    const { projectId } = await params
    const body = await request.json()
    const { action, ...data } = body
    
    if (!projectId) {
      return NextResponse.json(
        { success: false, error: 'Project ID is required' },
        { status: 400 }
      )
    }
    
    // Get project details for context
    const { data: project, error: projectError } = await supabaseAdmin
      .from('Project')
      .select(`
        *,
        tasks: Task(id, title, status, priority, estimatedHours, aiEstimatedHours),
        members: ProjectMember(userId, role, user: User(name, email))
      `)
      .eq('id', projectId)
      .single()
    
    if (projectError || !project) {
      return NextResponse.json(
        { success: false, error: 'Project not found' },
        { status: 404 }
      )
    }
    
    switch (action) {
      case 'generate-tasks':
        return await handleGenerateTasks(project, data)
      
      case 'analyze':
        return await handleAnalyzeProject(project, data)
      
      case 'estimate':
        return await handleEstimate(project, data)
      
      case 'suggest-assignments':
        return await handleSuggestAssignments(project, data)
      
      case 'chat':
        return await handleProjectChat(project, data)
      
      default:
        return NextResponse.json(
          { success: false, error: 'Invalid action. Use: generate-tasks, analyze, estimate, suggest-assignments, or chat' },
          { status: 400 }
        )
    }
  } catch (error: any) {
    console.error('POST /api/v1/projects/[projectId]/ai error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'AI operation failed' },
      { status: 500 }
    )
  }
}

// Generate tasks from project description or requirements
async function handleGenerateTasks(project: any, data: any) {
  const { requirements, complexity = 'medium', maxTasks = 50 } = data
  
  if (!requirements?.trim()) {
    return NextResponse.json(
      { success: false, error: 'Requirements are required for task generation' },
      { status: 400 }
    )
  }
  
  // In production, this would call OpenAI API
  // For now, return a mock response structure
  const mockTasks = generateMockTasks(project.name, requirements, complexity, maxTasks)
  
  return NextResponse.json({
    success: true,
    data: {
      tasks: mockTasks,
      totalEstimatedHours: mockTasks.reduce((sum, t) => sum + t.estimatedHours, 0),
      confidenceScore: 85,
      aiModel: 'gpt-4',
      apiCost: 0.03,
      message: 'Tasks generated successfully. Review and adjust before creating.',
    },
  })
}

// Analyze project health and get AI insights
async function handleAnalyzeProject(project: any, data: any) {
  const tasks = project.tasks || []
  
  // Calculate basic metrics
  const totalTasks = tasks.length
  const completedTasks = tasks.filter((t: any) => t.status === 'completed').length
  const blockedTasks = tasks.filter((t: any) => t.status === 'blocked').length
  const inProgressTasks = tasks.filter((t: any) => t.status === 'in_progress').length
  
  // Generate mock AI insights
  const insights = [
    {
      type: 'performance',
      severity: totalTasks > 0 && completedTasks / totalTasks > 0.5 ? 'success' : 'warning',
      title: 'Project Progress Analysis',
      description: `Project is ${totalTasks > 0 ? Math.round((completedTasks / totalTasks) * 100) : 0}% complete with ${inProgressTasks} tasks in progress.`,
      suggestedAction: blockedTasks > 0 ? `Address ${blockedTasks} blocked task(s) to improve flow.` : null,
    },
    {
      type: 'risk',
      severity: blockedTasks > 2 ? 'critical' : blockedTasks > 0 ? 'warning' : 'success',
      title: 'Blocked Tasks Risk',
      description: blockedTasks > 0 
        ? `${blockedTasks} task(s) are blocked and may delay project completion.`
        : 'No blocked tasks. Project flow is healthy.',
      suggestedAction: blockedTasks > 0 ? 'Review blocked tasks and identify dependencies or blockers.' : null,
    },
    {
      type: 'forecast',
      severity: 'info',
      title: 'Estimated Completion',
      description: `Based on current velocity, the project is estimated to complete on schedule.`,
      suggestedAction: 'Maintain current pace and address blockers promptly.',
    },
  ]
  
  return NextResponse.json({
    success: true,
    data: {
      projectHealth: blockedTasks > 2 ? 'at_risk' : blockedTasks > 0 ? 'needs_attention' : 'healthy',
      metrics: {
        totalTasks,
        completedTasks,
        blockedTasks,
        inProgressTasks,
        completionRate: totalTasks > 0 ? Math.round((completedTasks / totalTasks) * 100) : 0,
      },
      insights,
      aiModel: 'gpt-4',
      apiCost: 0.02,
    },
  })
}

// Estimate time for tasks
async function handleEstimate(project: any, data: any) {
  const { taskIds, taskDescriptions } = data
  
  // Generate mock estimates
  const estimates = (taskDescriptions || []).map((desc: string, idx: number) => ({
    taskId: taskIds?.[idx] || `temp-${idx}`,
    description: desc,
    estimatedHours: Math.round(Math.random() * 16 + 2), // 2-18 hours
    confidenceScore: Math.round(Math.random() * 20 + 75), // 75-95%
    complexity: ['low', 'medium', 'high'][Math.floor(Math.random() * 3)],
  }))
  
  return NextResponse.json({
    success: true,
    data: {
      estimates,
      totalEstimatedHours: estimates.reduce((sum: number, e: any) => sum + e.estimatedHours, 0),
      aiModel: 'gpt-4',
      apiCost: 0.01,
    },
  })
}

// Suggest optimal task assignments based on team availability and skills
async function handleSuggestAssignments(project: any, data: any) {
  const { taskIds } = data
  const members = project.members || []
  
  // Generate mock suggestions
  const suggestions = (taskIds || []).map((taskId: string) => ({
    taskId,
    suggestedAssignee: members[Math.floor(Math.random() * members.length)]?.userId || null,
    reasoning: 'Based on current workload and skill match.',
    confidence: Math.round(Math.random() * 20 + 70),
    alternatives: members.slice(0, 2).map((m: any) => m.userId),
  }))
  
  return NextResponse.json({
    success: true,
    data: {
      suggestions,
      teamWorkload: members.map((m: any) => ({
        userId: m.userId,
        name: m.user?.name,
        currentTasks: Math.floor(Math.random() * 5 + 1),
        capacity: Math.round(Math.random() * 30 + 60),
      })),
      aiModel: 'gpt-4',
      apiCost: 0.02,
    },
  })
}

// Chat with AI about the project
async function handleProjectChat(project: any, data: any) {
  const { message, conversationHistory = [] } = data
  
  if (!message?.trim()) {
    return NextResponse.json(
      { success: false, error: 'Message is required' },
      { status: 400 }
    )
  }
  
  // Generate context-aware mock response
  const tasks = project.tasks || []
  const completedTasks = tasks.filter((t: any) => t.status === 'completed').length
  const totalTasks = tasks.length
  
  // Simple response generation based on message keywords
  let response = ''
  const lowerMessage = message.toLowerCase()
  
  if (lowerMessage.includes('status') || lowerMessage.includes('progress')) {
    response = `The project "${project.name}" is ${totalTasks > 0 ? Math.round((completedTasks / totalTasks) * 100) : 0}% complete. You have ${completedTasks} of ${totalTasks} tasks completed. Would you like me to analyze any specific area?`
  } else if (lowerMessage.includes('risk') || lowerMessage.includes('problem')) {
    const blockedTasks = tasks.filter((t: any) => t.status === 'blocked').length
    response = blockedTasks > 0 
      ? `I've identified ${blockedTasks} blocked task(s) that need attention. Would you like me to analyze the blockers and suggest solutions?`
      : `No significant risks detected. The project is progressing well. Would you like me to run a detailed analysis?`
  } else if (lowerMessage.includes('create') || lowerMessage.includes('add') || lowerMessage.includes('task')) {
    response = `I can help you create tasks. Please describe what you need, and I'll generate a task breakdown with time estimates. For example: "Create a user authentication system with login, signup, and password reset."`
  } else if (lowerMessage.includes('assign') || lowerMessage.includes('who')) {
    response = `I can suggest optimal task assignments based on team workload and skills. Would you like me to analyze your team's current capacity and make recommendations?`
  } else {
    response = `I'm your AI Project Assistant for "${project.name}". I can help you:
    
• Create and breakdown tasks from requirements
• Estimate time for tasks
• Analyze project health and risks
• Suggest task assignments
• Answer questions about your project

What would you like me to help with?`
  }
  
  return NextResponse.json({
    success: true,
    data: {
      response,
      actions: [
        { label: 'Generate Tasks', action: 'generate-tasks' },
        { label: 'Analyze Project', action: 'analyze' },
        { label: 'Suggest Assignments', action: 'suggest-assignments' },
      ],
      aiModel: 'gpt-4',
      apiCost: 0.01,
    },
  })
}

// Helper function to generate mock tasks
function generateMockTasks(projectName: string, requirements: string, complexity: string, maxTasks: number) {
  const taskTemplates = [
    { phase: 'Planning', tasks: ['Requirements analysis', 'Technical specification', 'Architecture design', 'Project timeline'] },
    { phase: 'Design', tasks: ['UI/UX wireframes', 'Design system setup', 'Component library', 'Responsive layouts'] },
    { phase: 'Development', tasks: ['Backend API setup', 'Database schema', 'Authentication', 'Core features', 'Integration'] },
    { phase: 'Testing', tasks: ['Unit tests', 'Integration tests', 'User acceptance testing', 'Performance testing'] },
    { phase: 'Deployment', tasks: ['CI/CD pipeline', 'Staging deployment', 'Production deployment', 'Monitoring setup'] },
  ]
  
  const tasks: any[] = []
  const multiplier = complexity === 'simple' ? 0.5 : complexity === 'complex' ? 1.5 : 1
  
  taskTemplates.forEach((phase, phaseIdx) => {
    phase.tasks.forEach((taskTitle, taskIdx) => {
      if (tasks.length >= maxTasks) return
      
      const baseHours = [4, 8, 12, 16, 24][Math.floor(Math.random() * 5)]
      
      tasks.push({
        title: taskTitle,
        description: `${taskTitle} for ${projectName} - ${requirements.substring(0, 100)}...`,
        type: phaseIdx < 2 ? 'task' : 'feature',
        priority: phaseIdx === 0 ? 'high' : phaseIdx < 3 ? 'medium' : 'low',
        estimatedHours: Math.round(baseHours * multiplier),
        phase: phase.phase,
        dependencies: taskIdx > 0 ? [tasks[tasks.length - 1]?.title] : [],
        tags: [phase.phase.toLowerCase(), complexity],
        suggestedAssigneeRole: phaseIdx < 2 ? 'designer' : phaseIdx < 4 ? 'developer' : 'devops',
        checklist: [
          `Research ${taskTitle} requirements`,
          `Implement ${taskTitle}`,
          `Document ${taskTitle}`,
          `Review and test ${taskTitle}`,
        ],
      })
    })
  })
  
  return tasks.slice(0, maxTasks)
}
